'use client'

import { useState, useEffect } from 'react'
import { Line } from '@/types/transport'
import { mockStops } from '@/data/mock-data'
import { X } from 'lucide-react'

interface LineFormModalProps {
  line: Line | null
  onSave: (line: Omit<Line, 'id'>) => void
  onClose: () => void
}

export default function LineFormModal({
  line,
  onSave,
  onClose,
}: LineFormModalProps) {
  const [formData, setFormData] = useState({
    name: '',
    number: '',
    direction: '',
    city: '',
    stops: [] as string[],
    status: 'active' as const,
  })

  useEffect(() => {
    if (line) {
      setFormData({
        name: line.name,
        number: line.id,
        direction: line.direction,
        city: line.city,
        stops: line.stops,
        status: line.status,
      })
    }
  }, [line])

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (!formData.name || !formData.number || !formData.city) {
      alert('Veuillez remplir tous les champs requis')
      return
    }
    onSave({
      name: formData.name,
      id: formData.number,
      direction: formData.direction,
      city: formData.city,
      stops: formData.stops,
      status: formData.status,
    })
  }

  const handleStopToggle = (stopId: string) => {
    setFormData(prev => ({
      ...prev,
      stops: prev.stops.includes(stopId)
        ? prev.stops.filter(s => s !== stopId)
        : [...prev.stops, stopId]
    }))
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="flex justify-between items-center p-6 border-b border-gray-200 sticky top-0 bg-white">
          <h2 className="text-2xl font-bold text-gray-900">
            {line ? 'Modifier une Ligne' : 'Ajouter une Ligne'}
          </h2>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700"
          >
            <X size={24} />
          </button>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="p-6 space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Numéro de Ligne *
              </label>
              <input
                type="text"
                value={formData.number}
                onChange={(e) =>
                  setFormData(prev => ({ ...prev, number: e.target.value }))
                }
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="L01"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Ville *
              </label>
              <select
                value={formData.city}
                onChange={(e) =>
                  setFormData(prev => ({ ...prev, city: e.target.value }))
                }
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="">Sélectionner une ville</option>
                <option value="Agadir">Agadir</option>
                <option value="Marrakech">Marrakech</option>
                <option value="Casablanca">Casablanca</option>
                <option value="Rabat">Rabat</option>
                <option value="Fez">Fez</option>
              </select>
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Nom de la Ligne *
            </label>
            <input
              type="text"
              value={formData.name}
              onChange={(e) =>
                setFormData(prev => ({ ...prev, name: e.target.value }))
              }
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Centre Ville"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Direction
            </label>
            <input
              type="text"
              value={formData.direction}
              onChange={(e) =>
                setFormData(prev => ({ ...prev, direction: e.target.value }))
              }
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Ex: Gare Routière → Tiklouine"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Sélectionner les Arrêts
            </label>
            <div className="grid grid-cols-2 gap-2 max-h-48 overflow-y-auto border border-gray-200 rounded-lg p-3">
              {mockStops.map((stop) => (
                <label key={stop.id} className="flex items-center gap-2">
                  <input
                    type="checkbox"
                    checked={formData.stops.includes(stop.id)}
                    onChange={() => handleStopToggle(stop.id)}
                    className="w-4 h-4 cursor-pointer"
                  />
                  <span className="text-sm text-gray-700">{stop.name}</span>
                </label>
              ))}
            </div>
          </div>

          {/* Buttons */}
          <div className="flex gap-3 pt-4 border-t border-gray-200">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 border border-gray-300 rounded-lg text-gray-700 font-medium hover:bg-gray-50 transition-colors"
            >
              Annuler
            </button>
            <button
              type="submit"
              className="flex-1 px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg font-medium transition-colors"
            >
              {line ? 'Mettre à jour' : 'Créer'}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}
